*{
    font-family: sans-serif;
}

.grid-area{
    margin-top: 40px;
    table{
        max-width: 700px;
        margin: auto;
        user-select: none;
        td{
            width: 20px;
            height: 20px;
            &.selected{
                color: rgb(49, 6, 204);
            }
        }
    }
}